













import Vue from 'vue';
import { Component, Prop } from "vue-property-decorator";

@Component

export default class TypeTab extends Vue {
  @Prop() readonly types!: any[];
  @Prop() readonly type!: string;

  onClickIncome() {
    this.$emit('onClickIncome');
  }

  onClickExpenditure() {
    this.$emit('onClickExpenditure');
  }
}
